<template>
	<div class="manu" @click="type==0?next(manu,2):next(manu,0)">{{text}}</div>
</template>

<script>
	export default{
		//manu0type0
		props:['manu',"type"],
		inject:['next'],
		data(){
			return{
				text:''
			}
		},
		computed:{
			
		},
		created(){
			//
			if(this.type===0){
			  this.text=''	
			}else if(this.type===1){
				this.text=''
			}
		}
	}
</script>

<style lang="scss" scoped="scoped">
     $lan:#3C9BFF;
	.manu{
		width: 2.3rem;
		line-height:0.9rem ;
		color: $lan;
		border: 0.02rem solid $lan;
		border-radius: 0.12rem;
		text-align: center;
		margin-right: 0.5rem;
		flex-shrink: 0;
	}
</style>


